body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
}

button {
  padding: 16px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
  background-color: purple;
  color: white;
  border-radius: 8px;
  cursor: pointer;
}
h1{
  color: purple;
}

h3, h4 {
  color: purple;
  margin-bottom: 0;
}

/* general nav */
nav {
  border-bottom: 4px solid purple;
  text-align: center;
  position: fixed;
  top: 0;
  width: 100%;
  background: white;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline-block;
  padding-top: 16px;
  padding-bottom: 16px;
}

/* Text of nav headers */
nav a {
  text-decoration: none;
  color: purple;
  font-weight: bold;
  padding: 16px 32px;
}

nav a:hover {
  background:purple;
  color: white;
}

#page-body {
  max-width: 700px;
  margin: auto;
  padding-top: 64px;
}

.article-list-item {
  text-decoration: none;
  color: black;
  padding: 16px 0 0 0;
}
/* Preview paragraph for list item */
.article-list-item p {
  padding-bottom: 32px;
  border-bottom: 2px solid purple;
}

#add-comment-form {
  border-bottom: 2px solid purple;
  padding: 16px;
}

#add-comment-form h3 {
  margin-bottom: 16px;
}
#add-comment-form label{
  margin-right: 3vw;
}
#add-comment-form textarea{
  margin-right: 3vw;
}
#add-comment-form input, textarea {
  display: block;
  width: 300px;
  padding: 8px;
  border-radius: 8px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  border: 2px solid purple;
  margin-bottom: 16px;
}

#add-comment-form button {
  width: 300px;
}

#upvotes-section button, p {
  display: inline-block;
  margin-top: 2vh;
  margin-bottom: 2vh;
  margin-right: 16px;
}

.comment {
  border-bottom: 2px solid black;
  padding: 16px;
}